"use client";

import { useState, useEffect } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { useSession } from "next-auth/react";
import { useToast } from "@/components/ui/use-toast";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import { Textarea } from "@/components/ui/textarea";
import { Switch } from "@/components/ui/switch";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { cn } from "@/lib/utils";
import { ColorPicker } from "@/components/color-picker";
import { api } from "@/convex/_generated/api";
import { useQuery, useMutation } from "convex/react";
import { Skeleton } from "@/components/ui/skeleton";
import { Loader2, MessageSquare, Plus, Trash2 } from "lucide-react";
import { useChatStore } from "@/store/use-chat-store";

const formSchema = z.object({
  name: z.string().min(2, {
    message: "Bot name must be at least 2 characters.",
  }),
  description: z.string().optional(),
  model: z.string().min(1, {
    message: "Please select a model.",
  }),
  welcome_message: z.string().optional(),
  seed_prompt: z.string().optional(),
  primaryColor: z.string().optional(),
  bubbleColor: z.string().optional(),
  userBubbleColor: z.string().optional(),
  showWelcomeMessage: z.boolean().default(true).optional(),
});

const EditBotPage = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { toast } = useToast();
  const { data: session } = useSession();

  const bot = useQuery(api.bots.getBotById, { id: id || "" });
  const chats = useQuery(api.chats.getChatsByAssistantId, {
    assistant_id: id || "",
  });

  const [testInput, setTestInput] = useState("");
  const [testMessages, setTestMessages] = useState<
    {
      id: string;
      sender: "user" | "bot";
      content: string;
      timestamp: Date;
    }[]
  >([]);
  const [selectedChatId, setSelectedChatId] = useState<string | null>(null);

  const startChat = useMutation(api.chats.startChat);
  const sendMessage = useMutation(api.messages.sendMessage);
  const updateBot = useMutation(api.bots.updateBot);

  const currentChat = useChatStore((state) => state.currentChat);
  const setCurrentChat = useChatStore((state) => state.setCurrentChat);

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (chats && chats.length > 0) {
      handleSelectChat(chats[0].id);
    } else if (bot && bot.welcome_message) {
      setTestMessages([
        {
          id: "1",
          content: bot.welcome_message,
          sender: "bot",
          timestamp: new Date(),
        },
      ]);
    }
  }, [chats, bot]);

  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: bot?.name || "",
      description: bot?.description || "",
      model: bot?.model || "gpt-3.5-turbo",
      welcome_message: bot?.welcome_message || "",
      seed_prompt: bot?.seed_prompt || "",
      primaryColor: bot?.chatSettings?.primaryColor || "#4f46e5",
      bubbleColor: bot?.chatSettings?.bubbleColor || "#f9fafb",
      userBubbleColor: bot?.chatSettings?.userBubbleColor || "#000000",
      showWelcomeMessage: bot?.showWelcomeMessage || true,
    },
    mode: "onChange",
  });

  useEffect(() => {
    if (bot) {
      form.reset({
        name: bot.name,
        description: bot.description || "",
        model: bot.model,
        welcome_message: bot.welcome_message || "",
        seed_prompt: bot.seed_prompt || "",
        primaryColor: bot.chatSettings?.primaryColor || "#4f46e5",
        bubbleColor: bot.chatSettings?.bubbleColor || "#f9fafb",
        userBubbleColor: bot.chatSettings?.userBubbleColor || "#000000",
        showWelcomeMessage: bot.showWelcomeMessage || true,
      });
    }
  }, [bot, form]);

  async function onSubmit(values: z.infer<typeof formSchema>) {
    try {
      setLoading(true);
      await updateBot({
        id,
        name: values.name,
        description: values.description,
        model: values.model,
        welcome_message: values.welcome_message,
        seed_prompt: values.seed_prompt,
        chatSettings: {
          primaryColor: values.primaryColor,
          bubbleColor: values.bubbleColor,
          userBubbleColor: values.userBubbleColor,
        },
        showWelcomeMessage: values.showWelcomeMessage,
      });
      toast({
        title: "Success",
        description: "Bot updated successfully.",
      });
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to update bot. Please try again.",
        variant: "destructive",
      });
    } finally {
      setLoading(false);
    }
  }

  const handleSendTestMessage = async () => {
    if (!testInput.trim()) return;

    try {
      // Verificar si hay un chat activo, si no, crear uno nuevo
      let activeChatId = selectedChatId;

      if (!currentChat || chats.length === 0) {
        // Iniciar un nuevo chat
        const newChatResult = await startChat({
          assistant_id: id,
          user_id: session?.binding_id || "",
        });

        if (newChatResult) {
          activeChatId = newChatResult.id;
          setSelectedChatId(newChatResult.id);
        }
      }

      // Crear mensaje del usuario localmente para UI inmediata
      const userMessage = {
        id: crypto.randomUUID(),
        sender: "user" as const,
        content: testInput,
        timestamp: new Date(),
      };

      setTestMessages((prev) => [...prev, userMessage]);
      setTestInput("");

      // Enviar mensaje al backend
      await sendMessage({
        chatId: activeChatId,
        assistant_id: id,
        role: "user",
        content: testInput,
      });

      // Obtener la respuesta del asistente
      const assistantResponse = useChatStore
        .getState()
        .currentChat?.messages?.at(-1);

      if (assistantResponse && assistantResponse.role === "assistant") {
        const botMessage = {
          id: crypto.randomUUID(),
          sender: "bot" as const,
          content: assistantResponse.content,
          timestamp: new Date(),
        };
        setTestMessages((prev) => [...prev, botMessage]);
      }
    } catch (error) {
      console.error("Error sending message:", error);
      toast({
        title: "Error",
        description: "Failed to send message. Please try again.",
        variant: "destructive",
      });
    }
  };

  const handleCreateNewChat = async () => {
    try {
      const newChatResult = await startChat({
        assistant_id: id,
        user_id: session?.binding_id || "",
      });

      if (newChatResult) {
        setSelectedChatId(newChatResult.id);
        setTestMessages([
          {
            id: "1",
            content:
              assistant?.welcome_message || "Hello! How can I help you today?",
            sender: "bot",
            timestamp: new Date(),
          },
        ]);

        toast({
          title: "Success",
          description: "New chat session created.",
        });
      }
    } catch (error) {
      console.error("Error creating new chat:", error);
      toast({
        title: "Error",
        description: "Failed to create new chat. Please try again.",
        variant: "destructive",
      });
    }
  };

  const handleSelectChat = (chatId: string) => {
    setSelectedChatId(chatId);
    const selectedChatData = chats.find((chat) => chat.id === chatId);

    if (selectedChatData) {
      // Convertir mensajes del chat seleccionado al formato de testMessages
      const convertedMessages = selectedChatData.messages.map((msg, index) => ({
        id: `${chatId}-${index}`,
        content: msg.content,
        sender: msg.role === "user" ? ("user" as const) : ("bot" as const),
        timestamp: new Date(msg.created_at || Date.now()),
      }));

      setTestMessages(convertedMessages);
    }
  };

  const handleDeleteChat = async (chatId: string) => {
    if (chats.length <= 1) {
      toast({
        title: "Cannot delete",
        description: "You must have at least one chat session.",
        variant: "destructive",
      });
      return;
    }

    try {
      // Aquí deberías llamar a la función de tu store para eliminar el chat
      // await deleteChatFromStore(chatId);

      // Si el chat eliminado era el seleccionado, seleccionar otro
      if (selectedChatId === chatId) {
        const remainingChats = chats.filter((chat) => chat.id !== chatId);
        if (remainingChats.length > 0) {
          handleSelectChat(remainingChats[0].id);
        }
      }

      toast({
        title: "Success",
        description: "Chat session deleted.",
      });
    } catch (error) {
      console.error("Error deleting chat:", error);
      toast({
        title: "Error",
        description: "Failed to delete chat. Please try again.",
        variant: "destructive",
      });
    }
  };

  if (!bot) {
    return (
      <div className="container mx-auto p-4">
        <Card>
          <CardHeader>
            <CardTitle>
              <Skeleton className="h-6 w-80" />
            </CardTitle>
            <CardDescription>
              <Skeleton className="h-4 w-50" />
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid gap-4">
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="flex h-full">
      {/* Sidebar */}
      <div className="w-64 border-r flex-shrink-0 py-4">
        <div className="px-4 mb-4">
          <Button
            onClick={handleCreateNewChat}
            size="sm"
            className="w-full bg-transparent"
            variant="outline"
            disabled={loading}
          >
            {loading ? (
              <>
                <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                Creating...
              </>
            ) : (
              <>
                <Plus className="h-4 w-4 mr-2" />
                New Chat
              </>
            )}
          </Button>
        </div>
        <Separator />
        <div className="overflow-y-auto h-[calc(100vh-150px)]">
          {chats && chats.length === 0 ? (
            <div className="p-4 text-sm text-gray-500">No chats yet.</div>
          ) : (
            chats &&
            chats.map((chat) => (
              <div
                key={chat.id}
                className={cn(
                  "p-3 border-b cursor-pointer hover:bg-gray-100 dark:hover:bg-gray-800 transition-colors",
                  selectedChatId === chat.id &&
                    "bg-blue-50 dark:bg-blue-900/20 border-l-4 border-l-blue-500"
                )}
                onClick={() => handleSelectChat(chat.id)}
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1 min-w-0">
                    <p className="font-medium text-sm bg-transparent border-none outline-none w-full truncate">
                      {chat.title || `Chat ${chat.id.slice(-4)}`}
                    </p>
                    <p className="text-xs text-gray-500 mt-1 truncate">
                      {chat.messages.length > 0
                        ? chat.messages[
                            chat.messages.length - 1
                          ].content.substring(0, 50) + "..."
                        : "No messages yet"}
                    </p>
                    <p className="text-xs text-gray-400 mt-1">
                      {chat.messages.length} message
                      {chat.messages.length !== 1 ? "s" : ""}
                    </p>
                  </div>
                  {chats.length > 1 && (
                    <Button
                      onClick={(e) => {
                        e.stopPropagation();
                        handleDeleteChat(chat.id);
                      }}
                      size="sm"
                      variant="ghost"
                      className="text-red-500 hover:text-red-700 ml-2 p-1 h-auto"
                    >
                      <Trash2 className="h-3 w-3" />
                    </Button>
                  )}
                </div>
              </div>
            ))
          )}
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col">
        {/* Chat Messages */}
        <div className="flex-1 overflow-y-auto p-4 space-y-4 bg-gray-50 dark:bg-gray-950">
          {testMessages.map((message, index) => (
            <div
              key={message.id}
              className={`flex ${
                message.sender === "user" ? "justify-end" : "justify-start"
              }`}
            >
              {message.sender === "bot" && (
                <div className="mr-2 flex-shrink-0">
                  <div
                    className="rounded-full h-8 w-8 flex items-center justify-center"
                    style={{
                      background:
                        (bot && bot.chatSettings?.primaryColor) || "#4f46e5",
                    }}
                  >
                    <MessageSquare className="h-4 w-4 text-white" />
                  </div>
                </div>
              )}
              <div
                className={`max-w-[80%] sm:max-w-[70%] p-3 rounded-lg`}
                style={{
                  background:
                    message.sender === "user"
                      ? (bot && bot.chatSettings?.userBubbleColor) || "#000000"
                      : (bot && bot.chatSettings?.bubbleColor) || "#f9fafb",
                  color: message.sender === "user" ? "#fff" : "#000",
                }}
              >
                <p className="text-sm">{message.content}</p>
                <p className="text-xs mt-1 opacity-60">
                  {message.timestamp.toLocaleTimeString([], {
                    hour: "2-digit",
                    minute: "2-digit",
                  })}
                </p>
              </div>
              {message.sender === "user" && (
                <div className="ml-2 flex-shrink-0">
                  <div className="bg-gray-300 dark:bg-gray-700 rounded-full h-8 w-8 flex items-center justify-center">
                    <span className="text-xs font-medium text-gray-800 dark:text-white">
                      You
                    </span>
                  </div>
                </div>
              )}
            </div>
          ))}

          {/* Mostrar indicador de carga cuando se está enviando un mensaje */}
          {loading && (
            <div className="flex justify-start">
              <div className="mr-2 flex-shrink-0">
                <div
                  className="rounded-full h-8 w-8 flex items-center justify-center"
                  style={{
                    background:
                      (bot && bot.chatSettings?.primaryColor) || "#4f46e5",
                  }}
                >
                  <MessageSquare className="h-4 w-4 text-white" />
                </div>
              </div>
              <div className="bg-gray-100 dark:bg-gray-800 p-3 rounded-lg">
                <div className="flex items-center gap-2">
                  <Loader2 className="h-4 w-4 animate-spin" />
                  <span className="text-sm text-gray-600 dark:text-gray-400">
                    Typing...
                  </span>
                </div>
              </div>
            </div>
          )}
        </div>

        {/* Chat Input */}
        <div className="border-t p-4">
          <div className="flex items-center">
            <Input
              type="text"
              placeholder="Type your message..."
              value={testInput}
              onChange={(e) => setTestInput(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  handleSendTestMessage();
                }
              }}
              className="flex-1 mr-2"
            />
            <Button onClick={handleSendTestMessage} disabled={loading}>
              Send
            </Button>
          </div>
        </div>
      </div>

      {/* Settings Sidebar */}
      <div className="w-96 border-l flex-shrink-0 p-4">
        <Card>
          <CardHeader>
            <CardTitle>Edit Bot</CardTitle>
            <CardDescription>Customize your bot's settings.</CardDescription>
          </CardHeader>
          <CardContent>
            <Form {...form}>
              <form
                onSubmit={form.handleSubmit(onSubmit)}
                className="space-y-4"
              >
                <FormField
                  control={form.control}
                  name="name"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Name</FormLabel>
                      <FormControl>
                        <Input placeholder="Bot Name" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="description"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Description</FormLabel>
                      <FormControl>
                        <Textarea
                          placeholder="A brief description of the bot"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="model"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Model</FormLabel>
                      <Select
                        onValueChange={field.onChange}
                        defaultValue={field.value}
                      >
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="Select a model" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          <SelectItem value="gpt-3.5-turbo">
                            GPT 3.5 Turbo
                          </SelectItem>
                          <SelectItem value="gpt-4">GPT 4</SelectItem>
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="welcome_message"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Welcome Message</FormLabel>
                      <FormControl>
                        <Input
                          placeholder="Hello! How can I help you today?"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="seed_prompt"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Seed Prompt</FormLabel>
                      <FormControl>
                        <Textarea
                          placeholder="A prompt to guide the bot's behavior"
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <div className="grid grid-cols-3 gap-4">
                  <div>
                    <FormField
                      control={form.control}
                      name="primaryColor"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Primary Color</FormLabel>
                          <FormControl>
                            <ColorPicker
                              value={field.value}
                              onChange={field.onChange}
                            />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                  <div>
                    <FormField
                      control={form.control}
                      name="bubbleColor"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Bubble Color</FormLabel>
                          <FormControl>
                            <ColorPicker
                              value={field.value}
                              onChange={field.onChange}
                            />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                  <div>
                    <FormField
                      control={form.control}
                      name="userBubbleColor"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>User Bubble Color</FormLabel>
                          <FormControl>
                            <ColorPicker
                              value={field.value}
                              onChange={field.onChange}
                            />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                  </div>
                </div>

                <FormField
                  control={form.control}
                  name="showWelcomeMessage"
                  render={({ field }) => (
                    <FormItem className="flex flex-row items-center justify-between rounded-lg border p-4">
                      <div className="space-y-0.5">
                        <FormLabel>Show Welcome Message</FormLabel>
                        <FormDescription>
                          Display the welcome message when a new chat is
                          started.
                        </FormDescription>
                      </div>
                      <FormControl>
                        <Switch
                          checked={field.value}
                          onCheckedChange={field.onChange}
                        />
                      </FormControl>
                    </FormItem>
                  )}
                />

                <Button type="submit" disabled={loading}>
                  {loading ? (
                    <>
                      <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                      Updating...
                    </>
                  ) : (
                    "Update Bot"
                  )}
                </Button>
              </form>
            </Form>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default EditBotPage;
